<template>
<div>
    <router-link  to="/login"><img class="close-account-page" src='@/assets/havr-icon/close-drawer-icon.svg'></router-link>
    <div class="form-stepper-create-account">
      <div class="jumbo">
  <h2 ><b>Create your Havr account </b></h2>
  <p >Please enter or click a button to validate a line</p>
      </div>
  <br><br>

        <div :class="{'hide-form' : currentStep < stepper.one}" class="section-form">
     <label class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.one}">1</span>. Hello there, what's your firstname ?</label>
<div class="form-inline">
  <div class="form-group">
   
 
    <input v-model="user.first_name" :disabled="currentStep < stepper.one" type="text" class="form-control form-control-lg custom-size havr-input " id="inputForm" aria-describedby="emailHelp" placeholder="eg. Jhon" required>
    <div class="invalid-feedback">
          Please enter your first name.
        </div>
  </div>
  <button id="formButton" v-if="currentStep === stepper.one"  @click="currentStep++"   class="btn btn-primary havr-color margin-left" :class="{activeButton : currentStep === stepper.one}">NEXT</button>
</div>

        </div>

 <div :class="{'hide-form' : currentStep < stepper.two}" class="section-form">
<label class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.two}">2.</span> And your last name ?</label>
<div class="form-inline">
  <div class="form-group">
   
 
    <input v-model="user.last_name" :disabled="currentStep < stepper.two" type="text" class="form-control form-control-lg custom-size havr-input " id="inputForm" aria-describedby="emailHelp" placeholder="eg. Doe" required>
     <div class="invalid-feedback">
          Please enter your last name.
        </div>
  </div>
  <button id="formButton" v-if="currentStep === stepper.two"  @click="currentStep++"  class="btn btn-primary havr-color margin-left" :class="{activeButton : currentStep === stepper.two}">NEXT</button>
</div>
 </div>
  <div :class="{'hide-form' : currentStep < stepper.three}" class="section-form">
<label class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.three}" >3.</span> Whats your email ?</label>
<div class="form-inline">
  <div class="form-group">
   
 
    <input v-model="user.email" :disabled="currentStep < stepper.three" type="email" class="form-control form-control-lg custom-size havr-input " id="inputForm" aria-describedby="emailHelp" placeholder="eg. jhon@doe.com" required>
     <div class="invalid-feedback">
          Please enter your email.
        </div>
  </div>
  <button id="formButton" v-if="currentStep === stepper.three"  @click="checkEmailAndGoNextStep()"  class="btn btn-primary havr-color margin-left" :class="{activeButton : currentStep === stepper.three}">NEXT</button>
</div>
  </div>
   <div :class="{'hide-form' : currentStep < stepper.four}" class="section-form">
<label class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.four}" >4.</span> Time to choose password</label>
<div class="form-inline">
  <div class="form-group">
   
 
    <input  v-model="user.password" :disabled="currentStep < stepper.four" type="password" class="form-control form-control-lg custom-size havr-input " id="inputForm" aria-describedby="emailHelp" placeholder="8 characters min" required>
     <div class="invalid-feedback">
          Please enter a password.
        </div>
  </div>
  <button  id="formButton"v-if="currentStep === stepper.four"  @click="checkPassword()"  class="btn btn-primary havr-color margin-left" :class="{activeButton : currentStep === stepper.four}">NEXT</button>
</div>
   </div>
    <div :class="{'hide-form' : currentStep < stepper.five}" class="section-form">

<label class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.five}">5. </span> Please confirm password</label>
<div class="form-inline">
  <div class="form-group">
   
 
    <input v-model="checkPasswordData" :disabled="currentStep < stepper.five" type="password" class="form-control form-control-lg custom-size havr-input " id="inputForm" aria-describedby="emailHelp" placeholder="same as before" required>
     <div class="invalid-feedback">
          Please confirm your password.
        </div>
  </div>
  <button id="formButton" v-if="currentStep === stepper.five"  @click="checkSamePassword()" class="btn btn-primary havr-color margin-left" :class="{activeButton : currentStep === stepper.five}">NEXT</button>
</div>

    </div>
     <div :class="{'hide-form' : currentStep < stepper.six}" class="section-form">
<label  class="step-form" for="exampleInputEmail1"><span :class="{baseColor : currentStep === stepper.six}">6.</span> Finally, choose a username</label>

  <div class="form-group">
   
 
    <input v-model="user.username" :disabled="currentStep < stepper.six" type="text" class="form-control form-control-lg custom-size havr-input " id="inputForm" aria-describedby="emailHelp" placeholder="eg. jhon02" required>
     <div class="invalid-feedback">
          Please enter a username.
        </div>
  </div>
  <br>
  <button  :disabled="canCreateAccount" v-if="currentStep === stepper.six"  @click="checkUsernameAndGoNextStep()"  class="btn btn-primary havr-color margin-left" :class="{activeButton : currentStep === stepper.six}">CREATE ACCOUNT</button>

     </div>

<img class="side-brand" src="@/assets/img/havr-side-brand.png">
    </div>
</div>
</template>



<script>
import repository from "@/repository/users";
import notifiy from "vue-notification";
import Swal from "sweetalert2";
export default {
  data() {
    return {
      checkPasswordData: "",
      currentStep: 1,
      user: {
        email: "",
        username: "",
        first_name: "",
        last_name: "",
        password: ""
      },
      stepper: {
        one: 1,
        two: 2,
        three: 3,
        four: 4,
        five: 5,
        six: 6
      },
      passwordRegex: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d.*)(?=.*\W.*)[a-zA-Z0-9\S]{8,50}$/m,
      usernameRegex: /[^A-Za-z0-9]/gm,
      emailRegex: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    };
  },
  computed: {
    canCreateAccount() {
      for (const i in this.user) {
        if (this.user[i].trim() === "") return true;
      }
    }
  },
  mounted() {
const input = document.getElementById("inputForm");
input.addEventListener("keyup", function(event) {
  event.preventDefault();
  if (event.keyCode === 13) {
    document.getElementById("formButton").click();
    
  }
  document.getElementById("inputForm").focus();
});

  },
  methods: {
    checkPassword() {
      const regex = RegExp(this.passwordRegex);
      if (!regex.test(this.user.password)) return this.notifyBadPassword();
      else this.currentStep++;
    },
    checkSamePassword() {
      if(this.user.password !== this.checkPasswordData) return  this.$notify({
        message: "Error : Password dont match",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
      else this.currentStep++
    },
    async checkUsernameAndGoNextStep() {
      const regex = RegExp(this.usernameRegex);
      if (regex.test(this.user.username))
        return this.notifyBadFormatUsername();
      const res = await repository.checkUsernameOrEmail(this.user.username);
      if (res.already_exists)
        return this.notifyEmailOrUsernameError("Username");
      else this.createAccount();
    },
    async checkEmailAndGoNextStep() {
      const regex = RegExp(this.emailRegex)
      if(!regex.test(this.user.email)) return this.notifyBadFormatEmail()
      const res = await repository.checkUsernameOrEmail(this.user.email);
      if (res.already_exists) return this.notifyEmailOrUsernameError("Email");
      else this.currentStep++;
    },
    notifyEmailOrUsernameError(p) {
      this.$notify({
        message: "Error : " + p + " already taken",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    notifyBadFormatEmail(p) {
      this.$notify({
        message: "Error : email format is incorrect",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    notifyBadFormatUsername() {
      this.$notify({
        message: "Error : username format is incorrect",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    notifyBadPassword() {
      this.$notify({
        message: "Error : password format is incorrect",
        type: "danger",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
    },
    async createAccount() {
      if (this.checkPasswordData !== this.user.password) return;
      const user = { user: this.user };
      const resp = await repository.createUserAccount(user);
      this.successNotification();
    },
    successNotification() {
      return Swal({
        allowOutsideClick: false,
        title: "Success",
        text: "Your account has been created with success !",
        type: "success",
        showCancelButton: false,
        confirmButtonText: "Go to login page"
      }).then(result => {
        if (result.value) {
          this.$router.push("login");
        }
      });
    }
  }
};
</script>


<style>
.side-brand {
  position: fixed;
  right: 0;
  bottom: 0;
  width: 40%;
}

.form-stepper-create-account {
  position: absolute;
  left: 10em;
}

.section-form {
  margin-bottom: 2em;
}


.baseColor {
  color: #ff5c00 !important;
}

.close-account-page {
  position: fixed;
  right: 0;
  z-index: 1;
  margin: 3em;
}

.margin-left {
        position: relative;
    left: 1em;
}

.havr-color {
  min-width: 140px;
  z-index: 1;
  border: #ff5c00 !important;
  background: #ff5c00 !important;
   border-radius: 5px!important
}

.hide-form {
  opacity: 0.3;
}

.step-form {
  font-size: large;
